:root {
  /* Change this to change the appearance of the hexaons */
  --hex-width: 100px;
  --hex-between: 10px;

  /* Other hexagon dimentions */
  --hex-height: calc(var(--hex-width) / 1.73 /* sqrt(3) */);
  --hex-margin: calc(var(--hex-width) / 2);
  --hex-border: calc(var(--hex-margin) / 1.73 /* sqrt(3) */);
  --hex-transition: all 0.2s ease;

  /* Colors */
  --color-hex-default: #2a68c4;
  --color-hex-hover: #0a192f;
}

.Hexagon_hexagon__1kkvv {
  align-items: center;
  background-color: #2a68c4;
  background-color: var(--color-hex-default);
  cursor: pointer;
  display: flex;
  fill: white;
  height: calc(100px / 1.73 );
  height: var(--hex-height);
  justify-content: center;
  margin: calc(calc(100px / 2) / 1.73 ) 0;
  margin: var(--hex-border) 0;
  position: relative;
  transition: all 0.2s ease;
  transition: var(--hex-transition);
  width: 100px;
  width: var(--hex-width);
}

.Hexagon_hexagon__1kkvv::after,
.Hexagon_hexagon__1kkvv::before {
  border-left: calc(100px / 2) solid transparent;
  border-left: var(--hex-margin) solid transparent;
  border-right: calc(100px / 2) solid transparent;
  border-right: var(--hex-margin) solid transparent;
  content: '';
  left: 0;
  position: absolute;
  transition: all 0.2s ease;
  transition: var(--hex-transition);
  width: 0;
}
.Hexagon_hexagon__1kkvv::after {
  border-top: calc(calc(100px / 2) / 1.73 ) solid #2a68c4;
  border-top: var(--hex-border) solid var(--color-hex-default);
  top: 100%;
  width: 0;
}
.Hexagon_hexagon__1kkvv::before {
  border-bottom: calc(calc(100px / 2) / 1.73 ) solid #2a68c4;
  border-bottom: var(--hex-border) solid var(--color-hex-default);
  bottom: 100%;
}

.Hexagon_hexagon__1kkvv:hover {
  background-color: #0a192f;
  background-color: var(--color-hex-hover);
}
.Hexagon_hexagon__1kkvv:hover::after,
.Hexagon_hexagon__1kkvv:hover::before {
  border-top-color: #0a192f;
  border-top-color: var(--color-hex-hover);
  border-bottom-color: #0a192f;
  border-bottom-color: var(--color-hex-hover);
}

.Hexagon_hexagon__1kkvv > svg {
  height: 100%;
  transition: all 0.2s ease;
  transition: var(--hex-transition);
}

